.weather-app {
	color: #EEE;
	margin-top: -10vh;

	min-width: 35vw;
	min-height: 280px;
}
.weather-today {
	display: flex;
    border: 1px solid #EEE;
    border-radius: 4px;
    padding: 10px 20px;
}

h1.weather-today__section {
	margin-right: 30px;
}
div.weather-today__section:not(:last-of-type) {
	margin-right: 40px;
}

.weather-today__title {
	font-family: "Staatliches", cursive;
	font-size: 3rem;
	letter-spacing: 1px;
}

.weather-today__data-value {
	font-size: 2rem;
}

.weather-today__data-value, .weather-today__data-key {
	font-family: "Staatliches", cursive;
	text-align: center;
}
.weather-forecast-card {
	flex: 1 1;
	text-align: center;
}

.weather-forecast-card__day, .weather-forecast-card__temp {
	font-family: "Staatliches", cursive;
	font-size: 1.1rem;
	letter-spacing: 1px;
}

.weather-forecast-card__day--underline {
	text-decoration: underline;
}

.weather-forecast {
	display: flex;
	margin-top: 20px;
}

.loading-indicator {
	display: flex;
	justify-content: center;
	height: 30px;
	padding-top: 30px;
}

.loading-indicator div {
	background-color: #EEE;
	border-radius: 50%;
	width: 20px;
	height: 20px;
	box-shadow: 2px 1px #AAA;

	-webkit-animation: jump 1s infinite ease-in-out;

	        animation: jump 1s infinite ease-in-out;
}

.loading-indicator div:not(:last-of-type) {
	margin-right: 10px;
}

.loading-indicator div:nth-of-type(1) {
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
}
.loading-indicator div:nth-of-type(2) {
	-webkit-animation-delay: .1s;
	        animation-delay: .1s;
}
.loading-indicator div:nth-of-type(3) {
	-webkit-animation-delay: .2s;
	        animation-delay: .2s;
}

@-webkit-keyframes jump {
	0% {
		margin-top: 0;
	}
	15% {
		margin-top: -10px;
	}
	30%, 100% {
		margin-top: 0;
	}
}

@keyframes jump {
	0% {
		margin-top: 0;
	}
	15% {
		margin-top: -10px;
	}
	30%, 100% {
		margin-top: 0;
	}
}
.search-form {
	margin-bottom: 20px;
}

.search-form__input {
	background: none;
	border: none;
	outline: 0;

	border-bottom: 2px solid #EEE;
	width: 40%;
	-webkit-transition: width .2s ease;
	transition: width .2s ease;
	padding: 8px;

	color: #EEE;
	font-size: 1.4rem;
}
.search-form__input:focus {
	width: 100%;
}
.search-form__input::-webkit-input-placeholder {
	color: #CCC;
}
.search-form__input::-ms-input-placeholder {
	color: #CCC;
}
.search-form__input::placeholder {
	color: #CCC;
}

* {
	margin: 0;
	padding: 0;
}

html, body {
	width: 100%;
	height: 100%;
}
body {
	background-image: -webkit-linear-gradient( 315deg, #52E5E7 10%, #130CB7 100%);
	background-image: linear-gradient( 135deg, #52E5E7 10%, #130CB7 100%);
}

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	display: flex;
	align-items: center;
	justify-content: center;
}
